@import "_variables.scss";
@import "_global.scss";

*::-webkit-scrollbar {
  width: 0;
}

*::-webkit-scrollbar-thumb {
  width: 0;
}

.loading-page {
  position: fixed;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99999999;
  background-color: #fff;
  img {
    width: 50px;
  }
}



// HEADER STYLES
.header-container {
  background: #ffffff;
  box-shadow: 0px 8px 14px rgba(0, 0, 0, 0.05);
  .header {
    background-color: #ffffff;
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    z-index: 9999999;
    .moreInfo {
      display: none;
    }
    .header__child {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 70px;
      .hamburger {
        display: none;
        font-size: 26px;
        color: $textColor;
      }
      &1 {
        border-bottom: 2px solid rgba(247, 248, 250, 1);
      }
      .header__right-details {
        display: flex;
        align-items: center;
        gap: 40px;
        .address-box {
          display: flex;
          align-items: center;
          gap: 15px;
          h2 {
            font-size: 15px;
            font-weight: 500;
            color: $blue;
            font-family: $fontReadex;
          }
          p {
            font-size: 12px;
            font-weight: 400;
            font-family: $fontReadex;
          }
        }
      }

      .header__child-menu {
        width: 100%;
        &__list {
          display: flex;
          align-items: center;
          gap: 50px;
          &-item {
            font-size: 16px;
            line-height: 20px;
            font-family: $fontReadex;
            color: $textColor;
            &.active {
              color: $orange;
            }
          }
        }
      }

      .header__social-media {
        display: flex;
        align-items: center;
        .header__social-media__icons {
          display: inline-flex;
          gap: 40px;
          a {
            width: 24px;
            height: 24px;
            img {
              width: 100%;
              height: 100%;
            }
          }
        }
        span {
          font-size: 16px;
          font-weight: 400;
          font-family: $fontReadex;
          color: $textColor;
          margin-right: 20px;
          white-space: nowrap;
        }
      }
    }
  }
}

@media screen and (max-width: 900px) {
  .hamburger {
    display: inline-block !important;
  }

  .header {
    position: fixed;
    top: 0;
    width: 100%;
    right: 0;
    padding: 0 2rem;
    .header__right-details {
      flex-direction: column;
      position: fixed;
      width: 100%;
      right: 0;
      background-color: #ffffff;
      z-index: 9;
      bottom: 0;
      padding: 2rem;
      gap: 1rem !important;
      align-items: flex-start !important;
      box-shadow: 0 0 14px rgba(0, 0, 0, 0.05);
      left: -100%;
      transition: left 0.5s ease-in-out;
      .address-box {
        justify-content: flex-start !important;
      }
    }

    .header__child2 {
      height: 0 !important;
      padding: 0 !important;
    }

    .header__child-menu {
      position: fixed;
      right: 0;
      top: 70px;
      background-color: #fff;
      padding: 2rem;
      left: -100%;
      transition: left 0.5s ease-in-out;

      &__list {
        flex-direction: column;
      }
    }

    .header__child-menu.active {
      left: 0;
    }
  }

  .header__right-details.active {
    left: 0 !important;
  }

  .header__social-media {
    display: none !important;
  }

  .moreInfo {
    display: flex !important;
    width: 40px;
    height: 40px;
    align-items: center;
    justify-content: center;
    background-color: $lightGreen;
    border-radius: 50%;
    position: fixed;
    bottom: 2rem;
    right: 2rem;
    z-index: 9999;
    i {
      font-size: 24px;
      color: #fff;
    }

    &:active {
      transform: scale(0.95);
    }
  }
}

.footer {
  background-color: rgba(1, 22, 51, 1);
  padding: 80px 0;
  .footer-row {
    display: flex;
    .footer-left-details {
      width: 50%;
      display: flex;
      flex-wrap: wrap;
      gap: 30px;
      iframe {
        border-radius: 6px;
      }
      .footer_contact-list {
        li {
          display: flex;
          gap: 15px;
          margin-bottom: 30px;
          .footer_contact-list__details {
            h2 {
              color: #fff;
              font-size: 14px;
              font-weight: 400;
              line-height: 17.5px;
              margin-bottom: 5px;
            }

            p {
              color: #fff;
              font-size: 12px;
              font-weight: 400;
              line-height: 15px;
            }
          }
        }
      }
    }

    .footer-right-details {
      display: flex;
      width: 50%;

      flex-direction: column;

      .footer-right-details__lists {
        display: flex;
        flex-wrap: wrap;
        gap: 100px;
        margin-bottom: 30px;
        .footer__list {
          h1 {
            font-size: 16px;
            line-height: 20px;
            font-weight: 400;
            margin-bottom: 20px;
            color: #fff;
          }

          ul {
            li {
              color: #fff;
              font-size: 14px;
              font-weight: 400;
              margin-bottom: 10px;
            }
          }
        }
      }

      .footer-right-details__social {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: flex-start;
        gap: 30px;
        .social-icons {
          display: flex;
          gap: 1rem;
        }
        span {
          font-family: "Readex Pro";
          font-style: normal;
          font-weight: 400;
          font-size: 16px;
          line-height: 20px;
          color: #ffffff;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    padding: 70px 0 0 0;
  }

  .footer {
    padding: 40px 0!important;
  }

  .footer-right-details__lists {
    display: none!important;
  }
  .footer-right-details__social {
    flex-direction: column;
    border-top: 1px solid rgba(255,255,255,0.2);
    padding-top: 20px;
    .social-icons {
      display: flex;
      column-gap: 1rem;
    }
  }

  .footer-row {
    flex-direction: column;
    .footer-left-details {
      width: 100% !important;
    }

    .footer-right-details {
      width: 100% !important;
    }

  }

  .footer-map {
    width: 100%;
  }
  iframe {
    width: 100%!important;
  }
}

// main

.main {
  min-height: 80vh;
}

// hero
.hero {
  width: 100%;
  .hero-row {
    display: flex;
    flex-wrap: wrap;

    .hero-left {
      width: 50%;
      .content {
        height: 500px;
        display: flex;
        align-items: flex-start;
        justify-content: center;
        flex-direction: column;
        h1 {
          font-family: "DM Sans";
          font-style: normal;
          font-weight: 700;
          font-size: 34px;
          line-height: 48px;
          /* or 141% */

          letter-spacing: 0.03em;

          color: #ffffff;
        }

        p {
          font-family: "Readex Pro";
          font-style: normal;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          /* or 150% */

          margin: 20px 0 40px 0;
          color: #d2d2d2;
        }

        .btn {
          padding: 13px 40px;
          font-size: 16px;
        }
      }

      .brands {
        display: flex;
        align-items: center;
        width: 100%;
        justify-content: space-between;
        padding: 30px 50px;
      }
    }

    .hero-right {
      width: 50%;
      position: relative;
      .hero-img {
        margin-top: 40px;
        z-index: 0;
      }

      .play-info {
        position: absolute;
        padding: 20px 40px;
        z-index: 10;
        margin: 0 25px;
        border-radius: 6px;
        gap: 20px;
        position: relative;
        background-color: rgba(255, 255, 255, 1);
        display: flex;
        align-items: center;
        bottom: 170px;
        .play-btn {
          width: 38px;
          height: 38px;
        }

        span {
          font-size: 16px;
          font-weight: 400;
        }
      }
    }
  }
}

.hero::after {
  content: "";
  position: absolute;
  width: 60%;
  height: 500px;
  background-color: rgba(1, 22, 51, 1);
  left: 0;
  top: 140px;
  z-index: -1;
}

@media screen and (max-width: 900px) {
  .hero::after {
    width: 100%;
    top: 70px;
    // background-color: #fff;
  }

  .hero-row {
    flex-direction: column;
  }

  .hero-left,
  .hero-right {
    width: 100% !important;
    // h1 {
    //   color: rgba(1, 22, 51, 1)!important;
    // }
  }

  .hero-right {
    img {
      width: 100%;
    }
  }

  .brands {
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    img {
      margin: 0 auto;
    }
  }

  .play-info {
    span {
      font-size: 14px !important;
    }
  }
}

@media screen and (max-width: 500px) {
  .hero-right {
    height: 500px;
  }
  .play-info {
    flex-direction: column;
    text-align: center;
    bottom: 200px !important;
  }
}

.home-cources { 
    .home-cources__row-card {
      background: #ffffff;
      box-shadow: 0px 0px 14px rgba(0, 0, 0, 0.08);
      border-radius: 6px;
      overflow: hidden;

      .card-image {
        width: 100%!important;
        height: 250px;
        img {
          width: 100%!important;
          object-fit: cover;
        }
      }

      .card-body {
        padding: 20px;
        height: 120px;
        overflow: hidden;
        h1 {
          margin-bottom: 20px;
          font-family: "DM Sans";
          font-style: normal;
          font-weight: 500;
          font-size: 24px;
          line-height: 31px;
          
          transition: .5s ease;
          color: #46454c;
        }

        h1:hover{
          color: $orange;
        }

        p {
          font-family: "Readex Pro";
          font-style: normal;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          /* or 150% */

          color: #6f747b;
        }


        
      }

      .card-footer {
        padding: 10px 20px 20px 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 50px;
        &__item {
          font-family: "Readex Pro";
          font-style: normal;
          font-weight: 400;
          font-size: 16px;
          line-height: 24px;
          /* identical to box height, or 150% */
          color: #46454c;
          width: 50%;
        }

        &__item:nth-child(1) {
          border-right: 1px solid rgba(0, 0, 0, 0.08);
        }

        &__item:nth-child(2) {
          padding: 0 20px;
        }
      }
    } 
  .btn {
    margin: 40px auto 80px auto;
    display: block;
    padding: 13px 42px;
  }
}

// .marketing
