      .marketing-photo {
        width: 420px;
        height: 480px;
      }

      .year {
        position: absolute;
        width: 200px;
        height: 187.5px;
        bottom: 50px;
        right: 110px;
        background: #ff5722;
        border-radius: 30px 0px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;

        h1 {
          font-family: "Readex Pro";
          font-style: normal;
          font-weight: 500;
          font-size: 34px;
          line-height: 42px;
          color: #fff;
        }

        span {
          font-family: "Readex Pro";
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 20px;
          /* identical to box height */

          letter-spacing: -0.03em;

          color: #ffffff;
        }
      }
    }

    .marketing-right {
      width: 50%;
      h1 {
        font-family: "DM Sans";
        font-style: normal;
        font-weight: 700;
        font-size: 28px;
        line-height: 38px;
        margin-top: 30px;
        /* or 141% */

        letter-spacing: 0.03em;

        color: #46454c;
      }
      .marketing-paragraph {
        font-family: "Readex Pro";
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        /* or 150% */

        color: #6f747b;
        margin: 10px 0 70px 0;
      }

      .marketing-list {
        li {
          margin-bottom: 40px;
          display: flex;
          gap: 25px;
          align-items: center;
          justify-content: flex-start;

          .emplema {
            width: 74px;
            height: 74px;
            display: flex;
            align-items: center;
            justify-content: center;
            background: #ffffff;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.04);
            border-radius: 15px 0px;
          }

          h2 {
            font-family: "DM Sans";
            font-style: normal;
            font-weight: 500;
            font-size: 24px;
            line-height: 31px;
            letter-spacing: 0.03em;

            color: #46454c;
          }

          p {
            font-family: "Readex Pro";
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            /* or 150% */

            max-width: 300px;
            color: #6f747b;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .marketing {
    padding: 30px 0;
  }
  .marketing-row {
    .marketing-left {
      display: none;
    }
    .marketing-right {
      width: 100% !important;
    }
  }
}

// banner

.banner {
  width: 100%;
  background-image: url("../assets/images/home-banner.png");
  background-size: cover;
  background-position: center;
  .banner-row {
    display: flex;
    min-height: 400px;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding: 2rem;
    column-gap: 70px;
    row-gap: 30px;
    .banner-item {
      width: 260px;
      height: 260px;
      display: flex;
      flex-direction: column;
      box-sizing: border-box;
      border: 2px solid #ffffff;
      border-radius: 60px 0px;
      align-items: center;
      justify-content: center;
      img {
        width: 70px;
      }

      h2 {
        font-family: "Readex Pro";
        font-style: normal;
        font-weight: 500;
        font-size: 34px;
        line-height: 24px;
        /* or 71% */

        color: #fafcff;
        margin: 25px 0 11px 0;
      }

      span {
        font-family: "Readex Pro";
        font-style: normal;
        font-weight: 400;
        font-size: 24px;
        line-height: 24px;
        /* identical to box height, or 100% */

        color: #fafcff;
      }
    }
  }
}

// home-services
.home-services {
  padding: 80px 0;
  .services-heading {
    h1 {
      font-family: "DM Sans";
      font-style: normal;
      font-weight: 700;
      font-size: 34px;
      line-height: 44px;
      letter-spacing: 0.03em;
      color: #46454c;
    }

    .single-heading {
      text-align: center;
      margin-bottom: 40px;
    }

    .services-heading__row {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 40px;
      span {
        font-family: "Readex Pro";
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 25px;

        letter-spacing: 0.03em;
        margin-top: 7px;
        color: #6f747b;
      }

      .service-heading__link {
        font-family: "Readex Pro";
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 25px;
        /* identical to box height */

        text-decoration-line: underline;

        color: #46454c;
      }
    }
  }

  .services-row {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
    &__card {
      background: #ffffff;
      box-shadow: 0px 0px 14px rgba(0, 0, 0, 0.08);
      border-radius: 6px;
      display: flex;
      align-items: center;
      justify-content: flex-start;
      padding: 50px 0 50px 50px;
      gap: 20px;
      h1 {
        font-family: "Readex Pro";
        font-style: normal;
        font-weight: 500;
        font-size: 22px;
        line-height: 30px;
        color: #46454c;
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .services-heading {
    h1 {
      text-align: center;
    }
  }
  .services-heading__row {
    flex-direction: column;
    justify-content: flex-start !important;
    align-items: start !important;
    span {
      margin-bottom: 10px;
    }

    width: 100%;
    text-align: center !important;
    justify-content: center !important;
    display: block;
    .service-heading__link {
      display: block;
      text-align: center !important;
      width: 100%;
    }
  }

  .services-row {
    &__card {
      padding: 30px 0 30px 30px !important;
    }
  }
}

// home-news
.home-news {
  width: 100%;
  .home-news__heading {
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 700;
    font-size: 34px;
    line-height: 44px;
    letter-spacing: 0.03em;
    color: #46454c;
    margin-bottom: 40px;
  }

  .home-news__row {
    display: flex;
    gap: 20px;
    margin-bottom: 80px;
    .home-news__row-item {
      height: 367px;
      position: relative;
      border-radius: 6px;
      overflow: hidden;
      flex-wrap: wrap;
      filter: drop-shadow(0px 0px 14px rgba(0, 0, 0, 0.15));
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
      }

      .overlay {
        position: absolute;
        z-index: 1;
        height: 144px;
        width: 100%;
        padding: 18px 20px;
        background: rgba(0, 0, 0, 0.7);
        border-radius: 0px 0px 6px 6px;
        bottom: 0;
        .overlay-title {
          font-family: "Readex Pro";
          font-style: normal;
          font-weight: 400;
          font-size: 18px;
          line-height: 24px;
          color: #ffffff;
          height: 70px;
        }

        .news-details {
          width: 100%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          flex-wrap: wrap;
          position: absolute;
          bottom: 20px;
          span {
            font-family: "Inter";
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;

            color: #ffffff;
          }
        }
      }
    }

    .home-news__row-item:nth-child(1) {
      width: 450px;
    }
    .home-news__row-item:nth-child(2) {
      width: 262px;
    }
    .home-news__row-item:nth-child(3) {
      width: 356px;
    }
  }

}
.home-news__text {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 80px;
  margin-top: 40px;
  justify-content: center;

  &-left {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      width: 250px;
      margin: 0 auto;
    }
  }
  &-right {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: left;
    padding-left: 104px;
    font-family: "Readex Pro";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 34px;
    border-left: 5px solid #7c7c7c;
    /* or 142% */
    color: #46454c;
    p {
      max-width: 100%;
    }
  }
}

@media screen and (max-width: 768px) {
    .home-news__heading {
        width: 100%;
        text-align: center;
    }
  .home-news__row {
    width: 100%;
    flex-direction: column;
    &-item {
      width: 100% !important;
    }
  }

  .home-news__text {
    flex-direction: column;

    &-left {
      width: 100% !important;
      text-align: center;
      align-items: center;
      margin: 0;
      padding: 0 !important;
    }
    &-right {
      padding: 0 !important;
      width: 100% !important;
      text-align: center !important;
      align-items: center;
      margin: 0;
      border: none !important;
      margin-top: 20px;
    }
  }
}


.news-blog-heading {
  width: 100%;
  margin: 40px 0 80px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  .heading-row {
    width: 356px;
    padding: 10px;
    border: 2px solid #FF5722;
    border-radius: 6px;
    display: flex;
    &__item {
      width: 100%;
      font-family: 'DM Sans';
      font-style: normal;
      font-weight: 700;
      font-size: 24px;
      line-height: 31px;
      letter-spacing: 0.03em; 
      padding: 5px 0;
      text-align: center;
      border-radius: 3px;
      cursor: pointer;
      &.active {
        background: #FF5722;
