      border-radius: 6px;
      display: flex;
      &__item {
        width: 100%;
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 31px;
        letter-spacing: 0.03em; 
        padding: 5px 0;
        text-align: center;
        border-radius: 3px;
        cursor: pointer;
        &.active {
          background: #FF5722;
          color: #ffffff;
        }
      }
    }
  }
}



