.tns-outer {
  position: relative!important;
}
.tns-controls {
  position: absolute;
  z-index: 999;display: flex;
  width: 100%;
  justify-content: flex-end;
  top: -50px;
  right: 20px;
  gap: 0;

  button { 
  color: #fff;
  position: relative;
  background-color: #fff;
  width: 40px;
  height: 35px;
  z-index: 99;
  padding: 0!important;
  background-color: $orange;
  border: none;
  }


  button:nth-child(2) {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  }

  button:nth-child(1) {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    }
   
 
  button:nth-child(1)::before{
    content: '<';
    position: absolute;
    height: 100%;
    width: 100%;
    color: #fff;
    font-size: 24px;
    background-color: $orange;
    margin: 0!important;
    top: 0;
    left: 0;
    border-right: 1px solid rgba(255,255,255,0.3)!important;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    
  }

  button:nth-child(1)::before:hover {
    background-color: red;
  }
  button:nth-child(2)::before{
    content: '>';
    position: absolute;
    height: 100%;
    width: 100%;
    color: #fff;
    background-color: $orange;
    margin: 0!important;
    top: 0;
    font-size: 24px;
    
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  }
}


@media screen and (max-width: 500px) {
  .tns-controls{
    top: -40px;
    right: 5px;
  }

  .container {
    width: 100%;
    margin: 0;
    padding: 1rem;
    overflow-x: hidden;
  }

  .cources-more__details .more-details-content{
    padding: 20px!important;
  }

  .cources-more__details .more-details-content ul li {
    display: block!important;
  }

  .more-details-list__icon {
    margin-bottom: 1rem;
  }

  .home-news__text-left {
    img {
      width: 150px;   
    }
  }

  .home-news__text-right {
    p {
      font-size: 18px;
    }
  }
}

