@import './_global.scss';
@import './_variables.scss';
.underline {
    text-decoration: underline;
}
.admin-layout {
    background-color: $lightBg;
    min-height: 100vh;
    display: flex;
    position: relative;
    .sidebar {
        width: 260px;
        position: sticky;
        background-color: #fff;
        box-shadow: 0 0 5px rgba(0,0,0,.05);
        padding: 24px 0;
        transition: all .4s ease;
        top: 0;
        left: 0;

        .sidebar-all__items {
            height: 100vh;
            width: 100%;
            position: sticky!important;
            top: 0;
            padding: 24px 15px;
        }
        
        

        .toggle-btn {
            transition: all .3s ease;
            position: absolute;
            top: 24px;
            right: -20px;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            padding: 7px;
            background-color: $lightBg;
            cursor: pointer;

            span  {
                background-color: $blue;
                width: 26px;
                height: 26px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 50%;
            }
        }

        &__logo {
            width: 100%;
            margin-bottom: 33px;
            display: flex;
            align-items: center;
            justify-content: center;

            img {
                margin: 0 auto;
            }
        }

        &__menu {
            .title {
                font-style: normal;
                font-weight: 400;
                font-size: 13px;
                line-height: 20px;
                color: #A1ACB8;
                margin-bottom: 10px;
            }

            &-list {
                display: flex;
                flex-direction: column;
                &__item {
                    margin-top: 10px;
                    &-link {
                        padding: 12px 15px;
                        display: flex;
                        align-items: center;
                        justify-content: flex-start;
                        gap: 15px;
                        border-radius: 6px;
                        mix-blend-mode: normal;
                        position: relative;
                        transition: all .5s ease;

                        .text {
                            font-family: 'Readex Pro';
                            font-style: normal;
                            font-weight: 300;
                            font-size: 15px;
                            line-height: 19px;
                            color: #697A8D;
                        }


                        &:hover {
                            background-color: rgba(67, 89, 113, 0.06);
                        }

                        

                    }
                    &-link.active {
                        background-color: rgba(105, 108, 255, 0.1);
                        
                        span {
                            color: rgba(105, 108, 255, 1);
                        }

                        &::after{
                            content: '';
                            position: absolute;
                            height: 100%;
                            width: 4px;
                            background-color: rgba(105, 108, 255, 1);
                            border-top-left-radius: 6px;
                            border-bottom-left-radius: 6px;
                            right: -15px;
                        }
                    }
                }
            }
        }

    }
    .sidebar.collapsed {
        width: 60px;
        .sidebar__logo {
            display: none!important;
        }
        
        .sidebar-all__items {
            padding: 70px 5px;

        }

        .toggle-btn {
            transform: rotate(180deg);
            top: 24px;
        }

        .sidebar__menu {
            .title {
                display: none;
            }


            .sidebar__menu-list {
                .sidebar__menu-list__item {
                    .sidebar__menu-list__item-link {
                        justify-content: center;
                        .text {
                            display: none;
                        }
                    }

                    .sidebar__menu-list__item-link.active {
                        background-color: rgba(105, 108, 255, 0.1);
                        
                        span {
                            color: rgba(105, 108, 255, 1);
                        }

                        &::after{
                            content: '';
                            position: absolute;
                            height: 100%;
                            width: 4px;
                            background-color: rgba(105, 108, 255, 1);
                            border-top-left-radius: 6px;
                            border-bottom-left-radius: 6px;
                            right: -5px;
                        }
                    }
                }
            }
        }
    }

    


    .admin-main {
        padding: 15px 25px;
        width: 100%;
    }

    .admin-nav {
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 25px;
        border-radius: 8px;
        background-color: #fff;
        box-shadow: 0 0 5px rgba(0,0,0,.05);
        width: 100%;
        margin-bottom: 30px;

        .nav-search {
            label {
                display: flex;
            align-items: center;
            gap: 10px;

            input {
                border: none;
                outline: none;
            }
            }
        }


        .nav-right {
            .user-badge {
                width: 35px;
                height: 35px;
                border-radius: 50%;
                position: relative;
            img {
                border-radius: 50%;
            }

            background: linear-gradient(0deg, rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.6)), #696CFF;
            .status {
                position: absolute;
                width: 8px;
                height: 8px;
                border: 1.5px solid #fff;
                border-radius: 50%;
                background-color: rgba(113, 221, 55, 1);
                bottom: 0;
                right: 0;
            }
            }
        }
    }




    .admin-section {
        display: flex;
        flex-direction: column;
        gap: 30px;

        .admin-section__header {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            h1 {
                font-style: normal;
                font-weight: 500;
                font-size: 24px;
                line-height: 23px;
                color: #46454C;
            }

            .btn {
                padding: 8px 65px;
            }
        }
    }

}
@media screen and (max-width: 500px) {
    .admin-section__header {
        flex-direction: column;
        gap: 20px;
        .btn {
            width: 100%;
        }
    }
    .sidebar.collapsed {
       width: 50px;
       position: sticky;
    }
    
    
    .admin-main {
        padding: 10px!important;
    }
   }

.admin-login {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $lightBg;

    &__container {
        width: 400px;
        height: 500px;
        box-shadow: 0 0 5px rgba(0,0,0,.05);
        background-color: #fff;
        border-radius: 8px;
        padding: 30px 40px;
        display: flex;
        flex-direction: column;
        img {
            width: 150px;
            margin: 0 auto;
            margin-bottom: 40px;
        }

        form {
            width: 100%;
            display: flex;
            flex-direction: column;
            label {
                width: 100%;
                display: flex;
                flex-direction: column;
                margin: 10px 0;
                span {
                    font-size: 16px;
                    margin-bottom: 10px;
                }
                input {
                    padding: 8px 10px;
                    border-radius: 6px;
                    border: 1.5px solid $darkBg;
                }
            }

            button {
                margin-top: 20px;
            }
        }
    }
}

.second-window {
    width: 100%;
    .input-content-row {
        display: flex;
        gap: 70px;
        .input-content-row__item {
            display: flex;
            flex-direction: column;

            label {
                width: 100%;
                display: flex;
                flex-direction: column;
                margin-bottom: 20px;
                span {
                    font-family: 'Inter';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 24px;
                    color: #46454C;
                    margin-bottom: 10px;
                }

            } 
            .news-header {
                display: flex;
                justify-content: space-between;
                width: 100%;
                gap: 20px;
                
                input {
                    padding: 6px 10px;
                    height: 40px;
                    background-color: rgba(245, 246, 250, 1);
                    border: none;
                    outline: none;
                    border-radius: 8px;
                    width: 100%;
                } 

                .news-name {
                    width: 70%;
                }

                .news-date {
                    width: 30%;
                }

            }
        }

        .input-content-row__item.left {
            width: 70%;
        }
        .input-content-row__item.right {
            width: 30%;
        }
    }
    .file-input__view {
        background: #FFFFFF;
        border: 0.525424px dashed #ACACAC;
        border-radius: 4.20339px;
        width: 100%;
        height: 210px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;
        padding: 2rem 1rem;
        cursor: pointer;
        img {
            margin-bottom: 10px;
            width: 40px;
        }
        span {
            font-family: 'Readex Pro';
            font-style: normal;
            font-weight: 300;
            font-size: 10px;
            line-height: 11px;
            color: #46454C;
        }
    }

    textarea {
        width: 100%;
        background: #F5F6FA;
        box-shadow: 0px 8px 14px rgba(0, 0, 0, 0.01);
        border-radius: 8px;
        height: 254px;
        border: none;
        outline: none;
        padding: 8px 15px;
    }


    .main-img {
        border-radius: 6px;
        margin-top: 20px;
    }
}


.file-input {
    display: none;
    margin-right: 70px;
}



.header-flex {
    display: flex;
    gap: 20px;
}

.admin-container-with-nav__nav {
    width: 100%;
    display: flex;
    gap: 10px;
    li {
        padding: 8px 35px;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
        background-color: #fff;
        cursor: pointer;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 23px;
        color: #6F747B;
        border-top: 3px solid transparent;
        transition: all .5s cubic-bezier(0.075, 0.82, 0.165, 1);
        box-shadow: 0 0 5px rgba(0,0,0,.05);
    }

    li.active {
        color: #46454C;
        border-top: 3px solid $blue;
    }
}

.badge {
    font-size: 12px;
    color: #fff;
    border-radius: 6px;
    padding: 5px 10px;
    display: block;
    text-align: center;
    width: 120px!important;
    margin: 0 auto;
}
.badge.confirmed {
    background-color: $lightGreen!important;
}
.badge.deconfirmed {
    background-color: $orange!important;
}

.admin-content {
    background-color: #FFFFFF;
    padding: 30px 40px;
    box-shadow: 0 0 5px rgba(0,0,0,.05);
    border-radius: 8px;
    max-height: 700px;
    overflow-y: auto;
    table {
        width: 100%;
        thead {
            width: 100%;
            tr {
                padding: 10px 0;
                width: 100%;
                display: flex; 
                justify-content: space-between;
            }
        }

        tbody {
            width: 100%;
            tr {
                width: 100%;
                display: flex;
                justify-content: space-between;
                border-top: 1px solid rgba(230, 230, 230, 1); 
                border-bottom: 1px solid rgba(230, 230, 230, 1); 
                td {
                    padding: 10px 0;
                    
                    &:not(:last-child) {
                        width: 200px;
                    }

                    &:last-child {
                        width: 80px;
                    }

                    &.center{
                        display: flex;
                        justify-content: center;
                    }
                    
                }
            }
        }
    }
}




.navigation {
    display: flex;
    align-items: center;
    gap: 1rem;
    margin: 20px 0;
    &__item {
        cursor: pointer;
        width: 30px;
        height: 30px;
        border-radius: 6px;
        color: #fff;
        background-color: #696CFF;
        display: flex;
        align-items: center;
        justify-content: center;
        opacity: .6;
        &.active{
            opacity: 1;
        }
    }
}



@media screen and (max-width: 500px){
    .admin-content {
        padding: 10px 15px;
        
    }

    .input-content-row {
        flex-direction: column;

        .input-content-row__item.left {
            width: 100%!important;
            .news-header {
                flex-direction: column;
                .news-name {
                    width: 100%;

                }

                .news-date {
                    width: 100%;
                }
            }
        }
    }

    .input-content-row__item.right {
        width: 100%!important;
    }

    .file-input__view {
        width: 100%!important;
    }

    .header-flex {
        flex-direction: column!important;
        margin: 10px 0;
        width: 100%!important;
    }
}


.news-name-select {
    height: 40px;
    background-color: $lightBg;
    border: none;
    border-radius: 6px;
    padding: 0 10px;
}

.flex {
    display: flex;
    gap: 20px;
}

@media screen and (max-width: 500px) {
    .flex {
        flex-direction: column;
        .btn {
            width: 100%;
            margin-top: 20px;
        }
    }
}