@import '../../style/_global.scss';
@import '../../style/_variables.scss';    
.news-row {
    display: flex;
    gap: 20px;
    position: relative;
    .news-details {
        width: 70%;
        h1 {
            font-family: 'Inter';
            font-style: normal;
            font-weight: 500;
            font-size: 20px;
            line-height: 24px;

            color: #46454C;
        }

        .details {
            display: flex;
            flex-direction: column; 
            justify-content: center;
            margin: 20px 0;
            gap: 6px;
            span, i {
                font-family: 'Inter';
                font-style: normal;
                font-weight: 400;
                font-size: 12px;
                line-height: 15px;
                color: #A5A5A5;
                display: flex;
                align-items: center;
                gap: 6px;
            }
        }

        .main-img {
            filter: drop-shadow(0px 0px 14px rgba(0, 0, 0, 0.15));
            border-radius: 6px;
            width: 100%;
            margin: 20px 0 40px 0;
        }

        p {
            font-family: 'Inter';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            color: #6F747B;
        }
    }

    .news-list {
        width: 30%;
        
        .head {
            font-family: 'DM Sans';
            font-style: normal;
            font-weight: 500;
            font-size: 20px;
            line-height: 26px; 
            color: #000000;
            margin-bottom: 20px;
        }

        .date {
            display: flex;
            align-items: center;
            gap: 6px;
            margin-bottom: 20px;
            span {
                font-family: 'Inter';
                font-style: normal;
                font-weight: 400;
                font-size: 12px;
                line-height: 15px;
                color: #A5A5A5;
            }
        }

        ul {
            position: sticky!important;
            top: 0;
            background-color: #fff;
            overflow-y: auto;
            max-height: 100vh;
            li{
                padding: 20px 0;
                border-bottom: 1px solid #E9E9E9; 
                h2 {
                    font-family: 'Inter';
                    font-style: normal;
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 19px;

                    color: #46454C;
                }

                p {
                    font-family: 'Inter';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 24px;
                    color: #6F747B;
                }

                &:hover{
                    h2{
                        color: $orange;
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .news-row {
        flex-direction: column;
        .news-details {
            width: 100%;
        }

        .news-list {
            width: 100%;
        }
    }
}