@import '_variables.scss';
// global classes
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    font-family: $fontReadex;
    user-select: none;
}

a {
    text-decoration: none;
    color: unset;
}

img {
    max-width: 100%;
}

body, html {
    overflow-x: hidden;
    width: 100vw;
}

.bg-green {
    background-color: $lightGreen;
}

.carousel-flex { 
        width: 550px;
        img {
            height: 150px;
            object-fit: contain; 
        }
}

@media screen and (max-width: 900px) {
    .carousel-flex {
        margin-top: 40px;
    }
}

@media screen and (max-width: 600px) {
    .carousel-flex {
        width: 300px;
        margin: auto;
        img {
            width: 300px;
            margin: 0 auto;
        }
    }
}

.btn {
    font-size: 16px;
    font-weight: 400;
    color: #fff;
    border-radius: 6px;
    padding: 9px 20px;
    border: none;
    outline: none;
    transition: all .3s ease;
    cursor: pointer;
    opacity: 1;
    &:hover {
        opacity: 0.95;
    }

    &:active {
        transform: scale(0.95);
    }

    &:disabled{
        opacity: 0.7;
        cursor: not-allowed;
    }
}

.btn.btn-green {
    background: $lightGreen;
}

.btn.btn-green.outlined {
    background-color: transparent;
    color: $lightGreen;
    border: 2px solid $lightGreen;
}
.btn.btn-green.outlined:hover {
    background-color: $lightGreen;
    color: #fff;
}

.btn.btn-orange {
    background-color: $orange;
}
.container {
    width: $container;
    margin: 0 auto;
}

.container-fluid {
    width: $containerFluid;
    margin: 0 auto;
}

@media screen and (max-width: 1140px) {
    .container {
        width: 100%;
        padding: 0 2rem;
    }
}


@media screen and (max-width: 768px) {
    .container {
        width: 100%;
        padding: 0 1rem;
    }
}

.block {
    display: block;
}

.w-full {
    width: 100%;
}

.w-50{
    width: 50%;
}


.heading {
    width: 100%;
    text-align: center;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 34px;
    line-height: 44px;

    color: #46454C;
    margin: 80px 0  40px 0;
}
