@import '../../style/_variables.scss';
.cources {
    width: 100%;
    .cources-details {
        padding: 80px 0 50px 0;
        border-top: 1px solid rgba(233, 233, 233, 1);

        .cources-details__main {
            width: 100%;
            display: flex;
            &-left {
                width: 50%;

                img {
                    width: 564px;
                    height: 544px;
                    filter: drop-shadow(0px 0px 14px rgba(0, 0, 0, 0.15));
                    border-radius: 6px;
                    object-fit: cover;
                }
            }
            &-right {
                width: 50%;
                padding-left: 74px;
                padding-top: 40px;
                padding-bottom: 40px;
                position: relative; 

                h1 {
                    font-family: 'DM Sans';
                    font-style: normal;
                    font-weight: 700;
                    font-size: 34px;
                    line-height: 44px;

                    color: #46454C;
                }

                p {
                    font-family: 'DM sans';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 24px;
                    color: #6F747B;
                    padding-bottom: 45px;
                    padding-top: 7px;
                }

                .cource-details__list {
                    display: flex;
                    flex-direction: column;
                    gap: 20px;
                    li {
                        display: flex;
                        gap: 10px;
                        align-items: center;
                        .cource-details__list-icon {
                            width: 34px;
                            height: 34px;
                            background: #FFFFFF;
                            box-shadow: 0px 0px 14px rgba(0, 0, 0, 0.1);
                            border-radius: 10px 0px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                        }

                        span {
                            font-family: 'DM sans';
                            font-style: normal;
                            font-weight: 500;
                            font-size: 16px;
                            line-height: 24px;
                            /* identical to box height, or 150% */


                            color: #46454C;
                        }
                    }

                }
                .btn {
                    position: absolute;
                    bottom: 40px
                }
            }
        }
    }
}


@media screen and (max-width: 768px) {
    .cources-details__main {
        flex-direction: column;

        &-left, &-right {
            width: 100%!important;
        }

        &-left {
            img{
                width: 100%!important;
                height: 300px!important;
            }
        }

        &-right {
            padding: 2rem 0!important;
            .btn {
                position: unset!important;
                margin-top: 2rem;
            }
        }
    }
}

// cources-more__details
.cources-more__details {
    width: 100%;
    position: relative;
    .cources-background-image {
        position: absolute;
        right: -3rem;
        top: 4rem;
        z-index: -1;
        width: 547px;
    }
    &-navigation {
        position: sticky;
        top: 0;
        width: 100%;
        display: flex;
        gap: 80px;
        border-bottom: 2px solid #E9E9E9;
        background-color: #FFFFFF;
        overflow-x: auto;
        z-index: 9999;
        a {
            padding: 10px 0;
            display: inline-block;
            white-space: nowrap;
            position: relative;
        }

        a.active::after {
            content: '';
            position: absolute;
            height: 2px;
            background-color:  $orange;
            width: 100%;
            bottom: 0px;
            left: 0;
            border-radius: 5px;
        }
    }
    .more-details-container {
        padding-top: 40px;
        display: flex;
        flex-direction: column;
        gap: 20px;
        max-width: 70%;
    }

    .heading-title {
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        color: #46454C;
    }

    .more-details-content {
        padding: 40px;
        background-color: rgba(243, 248, 254, 1);
        border-radius: 6px;
        .more-details-list {
            width: 100%;
            display: flex;
            flex-direction: column;
            gap: 20px;

            li {
                display: flex;
                align-items: center;
                gap: 20px;
                .more-details-list__icon {
                    min-width: 34px!important;
                    width: max-content;
                    height: 34px; 
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background: #82B440;
                    border-radius: 10px 0px;
                }

                span {
                    font-family: 'Readex Pro';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 24px;
                    color: #000000;
                }
            }
        }

        .about-cource-row {
            display: flex; 
            column-gap: 92px;
            &__item {
                display: flex;
                flex-direction: column;
                .ac-icon{
                    width: 96px;
                    height: 96px; 
                    
                    background: #FFFFFF;
                    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.04);
                    border-radius: 15px 0px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }

                span {
                    margin: 20px auto;
                    font-family: 'Readex Pro';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 20px;  
                    color: #46454C;
                }

                h2 {
                    font-family: 'Readex Pro';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 34px;
                    line-height: 42px;
                    color: #46454C;
                }
            }
        }

        .about-modules {
            width: 100%;
            padding-top: 40px;
            margin-top: 40px;
            border-top: 2px solid #E9E9E9;
            
            font-family: 'Readex Pro';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px; 

            color: #46454C;

            ul {
                display: flex;
                flex-direction: column;
                gap: 10px;
            }
        }

        .mentor {
            display: flex;
            align-items: flex-start;
            gap: 40px;
            img{
                border-radius: 50%;
                object-fit: cover;
                filter: drop-shadow(0px 0px 4px rgba(0, 0, 0, 0.04));
            }

            h1 {
                font-family: 'DM Sans';
                font-style: normal;
                font-weight: 500;
                font-size: 20px;
                line-height: 24px;
                color: #46454C;
            }

            p {
                font-family: 'Readex Pro';
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 24px;
                color: #6F747B;
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .cources-more__details {
        width: 100%;
        position: relative; 
        &-navigation { 
            gap: 40px; 
            top: 70px;
            a {
                padding: 10px 0;
                display: inline-block;
                white-space: nowrap;
                position: relative;
            }
    
            a.active::after {
                display: none;
            }

            a.active {
                background-color: $orange;
                padding: .7rem .5rem;
                border-radius: 6px;
                color: #FFFFFF;
            }
        }
        .more-details-container { 
            max-width: 100%;
        }
    
        .heading-title {
            text-align: center;
        }
    
        .more-details-content { 
             
    
            .about-cource-row {
                display: flex; 
                justify-content: center;
                align-items: center;
                column-gap: 1rem;
                &__item {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    text-align: center;
                    .ac-icon{
                        width: 96px;
                        height: 96px; 
                        
                        background: #FFFFFF;
                        box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.04);
                        border-radius: 15px 0px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                    }
    
                    span {
                        margin: 20px auto;
                        font-family: 'Readex Pro';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 20px;  
                        color: #46454C;
                    }
    
                    h2 {
                        font-family: 'Readex Pro';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 34px;
                        line-height: 42px;
                        color: #46454C;
                    }
                }
            }
        }
    }

    .cources-background-image {
        display: none;
    } 
}