@import "../../style/_global.scss";
@import "../../style/_variables.scss";

.courseRequest__heading {
  width: 100%;
  margin: 50px 0;
  text-align: center;

  h1 {
    font-size: 30px;
    color: $textColor;
  }

  p {
    font-size: 16px;
    color: $lightTextColor;
    margin-top: 5px;
  }
}

.reg_row {
  display: flex; 
  gap: 1rem;
  align-items: center;
}

.courseRequest__form {
  flex: 1;
  width: 50%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  box-sizing: content-box;
  justify-content: flex-start!important;
  padding: 2rem;
  gap: 1rem;
  box-shadow: 0 0 15px rgba(0,0,0,.1);
  border-radius: 6px;
  background-color: #fff;
  position: relative;
  label {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: start;
    font-size: 14px;
    color: $lightTextColor;

    input, select, textarea {
      padding: 0.5rem;
      border-radius: 4px;
      outline: none;
      border: 1px solid rgba(0,0,0,.2);
      background-color: transparent;
      margin-top: 5px;
      color: $textColor;

      &:focus {
        border-color: $lightGreen;
      }
    }

    .btn {
      display: block;
    }

    select {
      cursor: pointer;
    }

    textarea {
      resize: none;
      width: 100%;
      height: 150px;
    }
  }
}

