.register-photo {
  width: 50%;
  position: relative;
  
  img {
    width: 200%!important;
  }
}


@media screen and (max-width: 700px) {
  .reg_row {
    flex-direction: column;
    form {
      width: 100%;
      box-sizing: border-box;
    }

    .register-photo {
      width: 100%;
    }
  }
}