@import '_variables.scss';
// global classes
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    font-family: $fontReadex;
}

a {
    text-decoration: none;
    color: unset;
}

img {
    max-width: 100%;
}


.btn {
    font-size: 16px;
    font-weight: 400;
    color: #fff;
    border-radius: 6px;
    padding: 8px 65px;
    border: none;
    outline: none;
    cursor: pointer;
    opacity: 1;
    &:hover {
        opacity: 0.95;
    }

    &:active {
        transform: scale(0.95);
    }
}

.btn.btn-primary {
    background: $blue;
}


.btn.btn-sm {
    border-radius: 4px;
    width: 24px;
    height: 24px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
 
.btn.btn-bordered {
    background: #FFFFFF;
    border: 0.5px solid #697A8D;
    border-radius: 4px;
    color: $textColor;
}
 

.table-btn {
    display: flex;
    gap: 20px;
}

