@import '../../style/_global.scss';
@import '../../style/_variables.scss';
img {
    max-width: 100%;
}
.services-hero {
    width: 100%;
    margin: 40px 0 80px 0;
    .services-hero__row {
        display: flex;
        align-items: center;
        &-item {
            width: 50%;
            h1 {
                font-family: 'DM Sans';
            font-style: normal;
            font-weight: 700;
            font-size: 34px;
            line-height: 48px;
            color: #46454C;
            }

            p {
                font-family: 'Readex Pro';
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 24px;
                color: #6F747B;
                margin: 20px 0 40px 0;
            }

            .btn {
                padding: 13px 40px;
            }
        }
    }
}


@media screen and (max-width: 768px) {
    .services-hero { 
        .services-hero__row { 
            flex-direction: column;
            justify-content: center;
            &-item {
                width: 100%;
                h1 { 
                    text-align: center;
                }
    
                p {
                    text-align: center;
                }
    
                .btn {
                    padding: 13px 40px;
                    display: block;
                    margin: 40px auto;
                }
            }
        }
    }
    
}

.single-service-row {
    display: flex;
    align-items: center;
    &__item {
        width: 50%;

        h1 {
            font-family: 'DM Sans';
            font-style: normal;
            font-weight: 500;
            font-size: 34px;
            line-height: 48px;
            color: #46454C;
        }

        p {
            font-family: 'Readex Pro';
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 24px;
            color: #46454C;
            margin: 10px 0 20px 0;
        }

        .single-service-row__item-list {
            margin-bottom: 40px;
            display: flex;
            flex-direction: column;
            gap: 20px;
            li {
                display: flex;
                gap: 15px;
                align-items: center;
                .number {
                    width: 34px;
                    height: 34px; 
                    background: #FF5722;
                    border-radius: 10px 0px;
                    color: #fff;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }

                span {
                    font-family: 'Readex Pro';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 20px;
                    line-height: 24px;
                    color: #46454C;
                }
            }
        }

        .tech {
            display: flex;
            flex-direction: column;
            h2 {
                font-family: 'Readex Pro';
                font-style: normal;
                font-weight: 400;
                font-size: 24px;
                line-height: 24px; 
                color: #46454C;
                margin-bottom: 20px;
            }

            &-row {
                display: flex;
                gap: 25px;
            }
        }
    }
}


@media screen and (max-width: 768px) {
    
.single-service-row {
    flex-direction: column;
    &__item {
        width: 100%;
  

        .single-service-row__item-list { 
            li {
                display: flex;
                gap: 15px;
                align-items: center;
                .number {
                    width: 34px;
                    height: 34px; 
                    background: #FF5722;
                    border-radius: 10px 0px;
                    color: #fff;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }

                span {
                    font-family: 'Readex Pro';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 20px;
                    line-height: 24px;
                    color: #46454C;
                }
            }
        }

        .tech {
            display: flex;
            flex-direction: column;
            h2 {
                font-family: 'Readex Pro';
                font-style: normal;
                font-weight: 400;
                font-size: 24px;
                line-height: 24px; 
                color: #46454C;
                margin-bottom: 20px;
            }

            &-row {
                display: flex;
                gap: 25px;
            }
        }
    }
}




}

.single-service-row__item {
    .main-img {
        width: 80%!important;
        border-radius: 8px;
    }
}


.modal-window {
    width: 550px;
    height: 440px;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;

    .body {
        width: 100%;
        display: fixed;
        box-shadow: 0 0 15px rgba(0,0,0,0.5);
    }
}